import React from "react";
import "./YouTubeItem.css";

function handleClick() {
    console.log("OK CA CLIQUE");
}

function replaceCharChelou(text) {
    text = text.replaceAll("&quot;", '"');
    text = text.replaceAll("&#39;", "'");
    return text;
};

function getTime(publishTime) {
    const now = new Date();
    const publishDate = new Date(publishTime);
    var diff = now - publishDate;
    diff /= 3600000;
    diff = Math.floor(diff);
    var diffStr = "";
    if (diff < 25) {
        diffStr = "il y a " + diff + " heures";
    } else {
        diff /= 24;
        diff = Math.floor(diff);
        diffStr = "il y a " + diff + " jours";
    }
    return diffStr;
};

export default function YouTubeItem({ video }) {
    const vignette = video.snippet.thumbnails.medium.url;
    const titre = replaceCharChelou(video.snippet.title);
    const idVideo = video.id.videoId;
    const baseUrl = "https://www.youtube.com/watch?v=";
    const url = baseUrl.concat(idVideo);
    var urlChannel = "https://www.youtube.com/channel/";
    const idChannel = video.snippet.channelId;
    urlChannel = urlChannel.concat(idChannel);
    const author = video.snippet.channelTitle;
    const publishTime = video.snippet.publishTime;
    const diffTime = getTime(publishTime);

    return (
        <div class="video" onClick={handleClick}>
            <img class="fit-picture" src={vignette} alt="Zut ! Ça marche pas" />
            <a href={url} class="titre" target="_blank" rel="noopener noreferrer">{titre}</a>
            <p class="chaine">
                <a href={urlChannel} target="_blank" rel="noopener noreferrer">{author}</a>
                <br />{diffTime}
            </p>
        </div>
    )
};