import React, { Component } from 'react';

class GoogleLoginComponent extends Component {
    componentDidMount() {
        this.googleSDK();
    }
    prepareLoginButton = () => {
        this.auth2.attachClickHandler('googleLoginBtn', {},
            (googleUser) => {
                let profile = googleUser.getBasicProfile();
                const loginData = googleUser.getAuthResponse();
                console.log(loginData);
                const nomUser = profile.getName();
                alert("Salut " + nomUser + ' !');
                this.props.setLoginData(loginData);
            }, (error) => {
                alert(JSON.stringify(error, undefined, 2));
            });
    }
    googleSDK = () => {
        window['googleSDKLoaded'] = () => {
            window['gapi'].load('auth2', () => {
                this.auth2 = window['gapi'].auth2.init({
                    client_id: process.env.REACT_APP_YOUR_CLIENT_ID,
                    cookiepolicy: 'single_host_origin',
                    scope: 'profile email https://www.googleapis.com/auth/youtube.force-ssl'
                });
                this.prepareLoginButton();
            });
        }
        (function (d, s, id) {
            var js, fjs = d.getElementsByTagName(s)[0];
            if (d.getElementById(id)) { return; }
            js = d.createElement(s); js.id = id;
            js.src = "https://apis.google.com/js/platform.js?onload=googleSDKLoaded";
            fjs.parentNode.insertBefore(js, fjs);
        }(document, 'script', 'google-jssdk'));
    }
    render() {
        return (
            <button className="loginBtn loginBtn--google boutton-rechercher" id="googleLoginBtn">
                Connexion
            </button>
        );
    }
}
export default GoogleLoginComponent;
