import React from 'react';
import CollapseCheck from './CollapseCheck';
import SearchBar from './SearchBar';
import { motsClefsData } from './motsClefsData';
import YouTubeItem from './YouTubeItem';
import TrierPar from './TrierPar';
import axios from 'axios';
import GoogleLoginComponent from './GoogleLoginComponent';
import './App.css';

function sleep(ms) {
  return new Promise(resolve => setTimeout(resolve, ms));
}

class App extends React.Component {
  state = motsClefsData;
  intToString = (n) => {
    var nStr = "";
    if (n < 10) {
      nStr = `0${n}`;
    }
    else {
      nStr = `${n}`;
    }
    return (nStr);
  };

  getOneWeekBefore = () => {
    var beginningQuery = new Date();
    beginningQuery.setDate(beginningQuery.getDate() - 7);
    var yearBQ = beginningQuery.getUTCFullYear();
    var monthBQ = beginningQuery.getUTCMonth() + 1;
    monthBQ = this.intToString(monthBQ);
    var dayBQ = beginningQuery.getUTCDate();
    dayBQ = this.intToString(dayBQ);
    var hoursBQ = beginningQuery.getUTCHours();
    hoursBQ = this.intToString(hoursBQ);
    var minutesBQ = beginningQuery.getUTCMinutes();
    minutesBQ = this.intToString(minutesBQ);
    const beginningQueryDateStr = `${yearBQ}-${monthBQ}-${dayBQ}T${hoursBQ}:${minutesBQ}:00Z`;
    return (beginningQueryDateStr);
  };

  setQueryYouTube = (queryYouTube) => {
    this.setState({ queryYouTube });
  };

  setOrdreResultats = (ordreResultats) => {
    this.setState({ ordreResultats });
    this.setState({ nextPageToken: '' });
  };

  setLoginData = (loginData) => {
    this.setState({ loginData });
  };

  getResearch = async () => {
    var params = {};
    var data = { items: [] };
    if ((typeof this.state.nextPageToken !== 'undefined') & (this.state.queryYouTube.length > 0)) {
      if (this.state.nextPageToken.length === 0) {
        const publishedAfter = this.getOneWeekBefore();
        this.setState({ publishedAfter });
        this.setState({ resultats: [] });
        params = {
          part: "snippet",
          q: this.state.queryYouTube,
          order: this.state.ordreResultats,
          maxResults: 48,
          type: "video",
          relevanceLanguage: "fr",
          regionCode: "FR",
          publishedAfter: publishedAfter
        };
      }
      else {
        params = {
          part: "snippet",
          q: this.state.queryYouTube,
          order: this.state.ordreResultats,
          maxResults: 48,
          type: "video",
          relevanceLanguage: "fr",
          regionCode: "FR",
          publishedAfter: this.state.publishedAfter,
          pageToken: this.state.nextPageToken
        };
      }
      data = await axios.get("https://youtube.googleapis.com/youtube/v3/search",
        {
          params: params,
          headers: {
            Authorization: `Bearer ${this.state.loginData.access_token}`,
            Accept: "application/json"
          }
        }
      ).then(res => res.data);
    }
    return (data);
  };

  handleResearch = async () => {
    const data = await this.getResearch();
    var resultats = this.state.resultats.slice();
    await sleep(111);
    resultats = resultats.concat(data.items);
    this.setState({ resultats });
    const nextPageToken = data.nextPageToken;
    this.setState({ nextPageToken });
  };

  handleVideoSelect = (video) => {
    this.setState({ selectedVideo: video });
  };

  removeElement = (id) => {
    const clients = this.state.clients.slice();
    const index = clients.findIndex(client => client.id === id);
    clients.splice(index, 1);
    this.setState({ clients: clients });
  };

  addElement = (client) => {
    const clients = this.state.clients.slice();
    clients.push(client);
    this.setState({ clients: clients });
  };

  addMotClef = (newMotClef) => {
    const recherches = this.state.recherches.slice();
    const motsClefs = recherches[1].children.slice();
    const newMotClefOuPas = motsClefs.find(elem => elem.label === newMotClef.label);
    if (!newMotClefOuPas) {
      motsClefs.push(newMotClef);
      recherches.splice(1, 1);
      const newMotsClefsBranch = {
        value: '/Mots clefs',
        label: 'Mots clefs',
        children: motsClefs
      };
      recherches.push(newMotsClefsBranch);
      this.setState({ recherches });
    }
  };

  handleScroll = (e) => {
    const bottom = e.target.scrollHeight - 10 < e.target.clientHeight + e.target.scrollTop;
    const hasToken = typeof this.state.nextPageToken !== 'undefined';
    if (bottom & hasToken) {
      if (this.state.nextPageToken.length > 0) {
        this.handleResearch();
      }
    }
  }

  resetNextPageToken = () => {
    this.setState({ nextPageToken: "" });
  }

  render() {
    return (
      <div>
        <div class="masthead">
          <div class="research-button">
            {(this.state.loginData !== null) ? (
              <button class="boutton-rechercher" onClick={this.handleResearch}>Rechercher</button>
            ) : (
              <GoogleLoginComponent setLoginData={this.setLoginData} />
            )
            }
          </div>
          <SearchBar class="new-key-word-bar" addMotClef={this.addMotClef} />
          <TrierPar setOrdreResultats={this.setOrdreResultats} />
        </div>
        <div class="frame-results" onScroll={this.handleScroll}>
          <div class="row justify-content-center video-container">
            {this.state.resultats.map(resultat => (
              <YouTubeItem class="col-auto" video={resultat} setOrdreResultats={this.setOrdreResultats} />
            ))}
          </div>
        </div>
        <div class="barre-laterale">
          <CollapseCheck nodes={this.state.recherches} setQueryYT={this.setQueryYouTube} resetNextPageToken={this.resetNextPageToken} />
        </div>
      </div>
    );
  };
}

export default App;
