import React, { Component } from "react";
import './SearchBar.css';

class SearchBar extends Component {
    state = {
        newMotClef: ''
    }

    handleChange = (e) => {
        this.setState({ newMotClef: e.target.value });
    }

    handleSubmit = (e) => {
        e.preventDefault();
        if (this.state.newMotClef.length === 0) {
            return;
        }
        const pathMotClef = '/Mots clefs/'
        const newMotClefElement = {
            value: pathMotClef.concat(this.state.newMotClef),
            label: this.state.newMotClef,
        };
        this.props.addMotClef(newMotClefElement);
    }

    render() {
        return <form class="form-search" onSubmit={this.handleSubmit}>
            <input class="input-search" type="text" placeholder="Nouveau mot clef" onChange={this.handleChange}></input>
            <button class="boutton-rechercher">Ajouter</button>
        </form>;
    }
}

export default SearchBar;