import React from 'react';
import CheckboxTree from 'react-checkbox-tree';
import "./CollapseCheck.css"

class CollapseCheck extends React.Component {
    state = {
        checked: [
        ],
        expanded: [
            '/Régions',
            '/Mots clefs'
        ],
    };

    constructor(props) {
        super(props);
        this.onCheck = this.onCheck.bind(this);
        this.onExpand = this.onExpand.bind(this);
    }

    onCheck(checked) {
        this.setState({ checked });
        let queryElements = [];
        const reg = new RegExp(/[^À-ÿà-ÿA-Za-z0-9]/g);
        for (const checkedElement of checked) {
            let checkedElementSplit = checkedElement.split('/');
            for (const elementSplit of checkedElementSplit) {
                let elementSplitNoDash = elementSplit.replaceAll(reg, '_');
                if ((!queryElements.includes(elementSplitNoDash)) & elementSplitNoDash !== '' & elementSplitNoDash !== "Mots_clefs" & elementSplitNoDash !== "Régions") {
                    queryElements.push(elementSplitNoDash);
                }
            }
        }
        const queryYouTube = queryElements.join("|");
        this.props.setQueryYT(queryYouTube);
        this.props.resetNextPageToken();
    }

    onExpand(expanded) {
        this.setState({ expanded });
    }

    render() {
        const { checked, expanded } = this.state;

        return (
            <div>
                <CheckboxTree
                    checked={checked}
                    expanded={expanded}
                    noCascade
                    nodes={this.props.nodes}
                    onCheck={this.onCheck}
                    onExpand={this.onExpand}
                />
            </div>
        );
    }
}

export default CollapseCheck;