import React from "react";
import './TrierPar.css';

export default function TrierPar({ setOrdreResultats }) {
    return (
        <div class="sort-by">
            <label class="label-tri">Trier par : </label>
            <select name="color" onChange={(e) => setOrdreResultats(e.target.value)}>
                <option value="date">La plus récente</option>
                <option value="relevance">Pertinence</option>
                <option value="rating">Mieux notées</option>
                <option value="title">Alphabétique</option>
                <option value="viewCount">Nombre de vues</option>
            </select>
        </div>
    )
};